import React from 'react';
import { useEffect } from 'react';
import { useParams, useLocation } from 'react-router-dom';
import styled from 'styled-components';
import useAxios from 'axios-hooks';
import ReviewItem from '../../components/items/ReviewItem';
import Heart from '../../components/Heart';


const DetailContainer=styled.div`
    box-sizing: border-box;
    padding: 15px;
    width: 80%;
    margin: auto;
`;

const ListDetail = () => {
    const location = useLocation();
    const { item } = location.state;

    let ref_id;
    let ref_type;
    
    if(item.place_no){
        ref_id = item.place_no;
        ref_type = 'place';
    }else if(item.accom_no){
        ref_id = item.accom_no;
        ref_type = 'accom';
    }else if(item.food_no){
        ref_id = item.food_no;
        ref_type = 'food';
    }
    
    /**   (place_no/accom_no/food_no)    get  */
    const [{data,loading,error},refetch] = useAxios({
        url:`/reviews/${ref_type}/${ref_id}`,
        method:'GET'
    },{useCache:false});
    // console.log(data.item);

    return (
        <DetailContainer>
            {/*  :     */}
            {/* <Heart ref_id={ref_id} ref_type={ref_type}></Heart> */}
            <h1>{item.title}</h1>
            <h1>{item.introduction}</h1>
            <h1>{item.address}</h1>
            <h1>{item.tag}</h1>
            <hr/>            
            <div className='review_wrap'>
                {
                    data && (
                        data.item.map((v,i)=>
                            <ReviewItem key={i} item={v}></ReviewItem>
                        )
                    )
                }
            </div>    
        </DetailContainer>
    );
};

export default ListDetail;