import React, { useCallback, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import styled from "styled-components";
import {useInView} from 'react-intersection-observer';
import useMountedRef from '../hooks/useMountedRef';

//Redux
import { useSelector, useDispatch } from "react-redux";
import { getPlaceList, addPlaceList } from "../slices/PlaceSlice";
import { getAccomList } from "../slices/AccomSlice";
import { getFoodList } from "../slices/FoodSlice";
import { getIsLogin } from '../slices/MemberSlice';


import Spinner from '../components/Spinner';
import TabArea from "../components/TabArea";
import ListItem from "../components/items/ListItem";
import HashtagBtn from "../components/buttons/HashtagBtn";
import Heart from "../components/Heart";
import { getMyLikeList } from "../slices/MyLikeSlice";


const TabPageContainer = styled.div`
    .content_wrap {
        width: 80%;
        margin: auto;
        margin-top: 20px;
        .hashtag_wrap {
            display: flex;
            flex-wrap: nowrap;
            overflow-x: auto;
            -ms-overflow-style: none;
            
            &::-webkit-scrollbar {
                display: none;
            }
            
            a{
                flex: 0 0 auto;
            }
        }
        .list_wrap{
            .item_wrap{
                display: flex;
            }
        }
    }
`;

const TabPage = () => {
    
    //path  
    const { api } = useParams();
    let tagArr;
    if (api === "place") {
        tagArr = ["", "", "", "", ""];
    } else if (api === "accom") {
        tagArr = ["accom", "accom", "accom", "", ""];
    } else if (api === "food") {
        tagArr = ["food", "food", "food", "", ""];
    }
    
    /** redux */
    const dispatch = useDispatch();
    const { data, loading, error } = useSelector((state) => state[api]);  
    const { data:loginData} = useSelector((state) => state.member);    
    // console.log(data);

    /** */
    // let currentPage = 1;
    const [ref, inView] = useInView();
    const [currentPage, setCurrentPage] = useState(1);
    // console.log("  : "+currentPage);
    //
    let queryKeyword = null;
    //  
    const [isEnd, setIsEnd] = useState(false);
    const [query, setQuery] = useState("");


    //    -->   ""  
    //tab  ,
    useEffect(() => {
        if(loginData){
            dispatch(getIsLogin());
        }
        //api(place,accom,food)  dispatch 
        if (api === "place") {
            dispatch(getPlaceList());
        } else if (api === "accom") {
            dispatch(getAccomList());
        } else if (api === "food") {
            dispatch(getFoodList());
        }
    },[dispatch,api]);
    

    /**   */
    const onTagClick = useCallback((e) => {
        e.preventDefault();
        //     
        const activeQuery = e.target.innerHTML.slice(1); 
        // console.log("query :"+ query)
        setQuery(activeQuery);
        // console.log("query :"+ query)
        dispatch(getPlaceList({query:activeQuery}));
        // css
    },[]);

    const mountedRef = useMountedRef();
    //_     
    useEffect(()=>{
        // window.addEventListener('scroll', e => {
            // const scrollTop = Math.ceil(window.scrollY);
            // const windowHeight = window.screen.availHeight;
            // const documentHeight = document.body.scrollHeight;
            // if (scrollTop + windowHeight >= documentHeight) {   

                if(mountedRef.current){
                    // setIsEnd(data.pagenation.isEnd);
                    if(inView && !loading && !data.pagenation.isEnd){
                        // currentPage++;
                        setCurrentPage(currentPage=>currentPage+1);
                        // console.log("  : "+ currentPage);
                        dispatch(addPlaceList({page:currentPage}));
                    };
                }
        
            // }
        // });
    },[mountedRef,inView,currentPage,isEnd]);

    return (
        <TabPageContainer>
            <TabArea/>

            <Spinner visible={loading} />
            
            <div className="content_wrap">
                <div className="hashtag_wrap">
                    {tagArr.map((v, i) => (
                        <HashtagBtn key={i} to="" onClick={onTagClick}>
                            {v}
                        </HashtagBtn>
                    ))}
                </div>
                <div className="list_wrap">
                    {data &&
                        data.item.map((v, i) => {
                            return (
                                <div key={i} className="item_wrap">
                                    <ListItem item={v} api={api}></ListItem>
                                    {loginData&&(
                                        <Heart item={v}></Heart>
                                        )
                                    }
                                </div>
                            ) 
                        })}
                </div>
                <div ref={ref} />
            </div>
        </TabPageContainer>
    );
};

export default TabPage;
